<template>
  <div class="standard-rate container">
    <el-table
      :data="rateData"
      border
      style="width: 100%">
      <el-table-column
        :label="$t('ratesStandard.part1')"
        >
        <template slot-scope="scope">{{ scope.row.currency_double }}</template>
      </el-table-column>
      <el-table-column
        :label="$t('ratesStandard.part2')"
        >
        <template slot-scope="scope">{{ scope.row.currency_buy_fee }}%</template>
      </el-table-column>
      <el-table-column
        :label="$t('ratesStandard.part3')">
        <template slot-scope="scope">{{ scope.row.currency_sell_fee }}%</template>
      </el-table-column>
    </el-table>
  </div>
    
</template>

  <script>
  import { getCurrencyTradeFee } from '@/api/ratesStandard'
  export default {
    data() {
      return {
        rateData: null
      }
    },
    created() {
      this.getTableFee()
    },
    methods: {
      getTableFee() {
        getCurrencyTradeFee().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.rateData = res.data
        }).catch(() => {
        })
      }
    }
  }
  </script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .standard-rate {
    min-height: 600px;
    padding: 50px 0;
  }
</style>



